import Scene3DTrasform from './3D/scene3dtransform'
import PancakeFieldsEditor from './UIElements/PancakeFieldsEditor'
import React, { Component } from "react";
import QDa from "./shapes/QDa.js";
import TR1a from "./shapes/TR1a.js";

import * as THREE from "three";


export default class PancakeElement extends Component {
  constructor(props) {
    super(props);
    this.state = {currentShape: new TR1a()};

  }

  render () {
    return (
      <div>
        source of truth A = {this.state.currentShape.properties.a}
        <PancakeFieldsEditor shape={this.state.currentShape}/>
        <Scene3DTrasform shape={this.state.currentShape}/>
      </div>
    )
  }
}
