import React, { Component } from "react";
import PropTypes from 'prop-types';
import * as THREE from "three";
import {assignUVs} from "../3D/shaper.js"

export default class TR1a {

  constructor() {
      this.properties = {
        a: 500,
        b: 300,
        d: 200,
        w: 200,
        L: 600,
        e: 300,
        f: 150,
        l3: 100
      };
  }

  getGeometry() {
    var geometry = new THREE.Geometry();

    var offsetY = this.properties.a/2 +  this.properties.l3/2;
    var offsetX = this.properties.f - this.properties.b/2;
    var offsetZ = -this.properties.e + this.properties.L/2;

    geometry.vertices.push(
      new THREE.Vector3(-this.properties.b/2, -this.properties.a/2,  this.properties.L/2),  // 0
      new THREE.Vector3( this.properties.b/2, -this.properties.a/2,  this.properties.L/2),  // 1
      new THREE.Vector3(-this.properties.b/2,  this.properties.a/2,  this.properties.L/2),  // 2
      new THREE.Vector3( this.properties.b/2,  this.properties.a/2,  this.properties.L/2),  // 3
      new THREE.Vector3(-this.properties.b/2, -this.properties.a/2, -this.properties.L/2),  // 4
      new THREE.Vector3( this.properties.b/2, -this.properties.a/2, -this.properties.L/2),  // 5
      new THREE.Vector3(-this.properties.b/2,  this.properties.a/2, -this.properties.L/2),  // 6
      new THREE.Vector3( this.properties.b/2,  this.properties.a/2, -this.properties.L/2),  // 7

      new THREE.Vector3(-this.properties.d/2 + offsetX, -this.properties.l3/2 + offsetY,  this.properties.w/2 + offsetZ),  // 8
      new THREE.Vector3( this.properties.d/2 + offsetX, -this.properties.l3/2 + offsetY,  this.properties.w/2 + offsetZ),  // 9
      new THREE.Vector3(-this.properties.d/2 + offsetX,  this.properties.l3/2 + offsetY,  this.properties.w/2 + offsetZ),  // 10
      new THREE.Vector3( this.properties.d/2 + offsetX,  this.properties.l3/2 + offsetY,  this.properties.w/2 + offsetZ),  // 11
      new THREE.Vector3(-this.properties.d/2 + offsetX, -this.properties.l3/2 + offsetY, -this.properties.w/2 + offsetZ),  // 12
      new THREE.Vector3( this.properties.d/2 + offsetX, -this.properties.l3/2 + offsetY, -this.properties.w/2 + offsetZ),  // 13
      new THREE.Vector3(-this.properties.d/2 + offsetX,  this.properties.l3/2 + offsetY, -this.properties.w/2 + offsetZ),  // 14
      new THREE.Vector3( this.properties.d/2 + offsetX,  this.properties.l3/2 + offsetY, -this.properties.w/2 + offsetZ),  // 15
    );

    /*
          6----7
         /|   /|
        2----3 |
        | |  | |
        | 4--|-5
        |/   |/
        0----1
     */

    geometry.faces.push(
      // front
      //new THREE.Face3(0, 3, 2),
      //new THREE.Face3(0, 1, 3),
      // right
      new THREE.Face3(1, 7, 3),
      new THREE.Face3(1, 5, 7),
      // back
      //new THREE.Face3(5, 6, 7),
      //new THREE.Face3(5, 4, 6),
      // left
      new THREE.Face3(4, 2, 6),
      new THREE.Face3(4, 0, 2),
      // top
      //new THREE.Face3(2, 7, 6),
      //new THREE.Face3(2, 3, 7),
      // bottom
      new THREE.Face3(4, 1, 0),
      new THREE.Face3(4, 5, 1),

      // +8
      // front
      new THREE.Face3(8, 11, 10),
      new THREE.Face3(8, 9, 11),

      // right
      new THREE.Face3(9, 15, 11),
      new THREE.Face3(9, 13, 15),
      // back
      new THREE.Face3(13, 14, 15),
      new THREE.Face3(13, 12, 14),
      // left
      new THREE.Face3(12, 10, 14),
      new THREE.Face3(12, 8, 10),
      // top
      //new THREE.Face3(10, 15, 14),
      //new THREE.Face3(10, 11, 15),
      // bottom
      //new THREE.Face3(12, 9, 8),
      //new THREE.Face3(12, 13, 9),

      //topmain
      new THREE.Face3(2, 9, 8),
      new THREE.Face3(2, 3, 9),

      new THREE.Face3(3, 13, 9),
      new THREE.Face3(3, 7, 13),

      new THREE.Face3(7, 12, 13),
      new THREE.Face3(7, 6, 12),

      new THREE.Face3(6, 8, 12),
      new THREE.Face3(6, 2, 8),

    );

    geometry.rotateX(Math.PI/4)
    geometry.rotateY(Math.PI/4)
    geometry.rotateZ(Math.PI)

    geometry.uvsNeedUpdate = true;
    geometry.buffersNeedUpdate = true;

    geometry.computeBoundingSphere();
    geometry.computeFaceNormals();
    assignUVs(geometry);

    return geometry;
  }


}
