import React, { Component } from "react";

import * as THREE from "three";
import { TransformControls } from 'three/examples/jsm/controls/TransformControls.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

import stealTexture from '../textures/blacha.gif';
//import QDa from "../shapes/QDa.js";


//https://threejsfundamentals.org/threejs/lessons/threejs-custom-geometry.html
class Scene3DTrasform extends Component {
  componentDidMount() {

    var cameraPersp, cameraOrtho, currentCamera;
    var scene, renderer, control, orbit;

    renderer = new THREE.WebGLRenderer();
    renderer.setPixelRatio( window.devicePixelRatio );
    renderer.setSize( window.innerWidth, window.innerHeight );
    document.body.appendChild( renderer.domElement );

    const aspect = window.innerWidth / window.innerHeight;

    cameraPersp = new THREE.PerspectiveCamera( 50, aspect, 0.01, 30000 );
    cameraOrtho = new THREE.OrthographicCamera( - 600 * aspect, 600 * aspect, 600, - 600, 0.01, 30000 );
    currentCamera = cameraPersp;

    currentCamera.position.set( 1000, 500, 1000 );
    currentCamera.lookAt( 0, 200, 0 );

    scene = new THREE.Scene();
    scene.add( new THREE.GridHelper( 1000, 10 ) );

    var light = new THREE.DirectionalLight( 0xffffff, 1.25 );
    light.position.set( 2, 2, 2 );
    scene.add( light );

    var light2 = new THREE.DirectionalLight( 0xaaaaaa, 1.5 );
    light2.position.set( -12, -12, -12 );
    scene.add( light2 );


    //https://stackoverflow.com/questions/35540880/three-textureloader-is-not-loading-images-files
    var texture = new THREE.TextureLoader().load( stealTexture, render );
    texture.anisotropy = renderer.capabilities.getMaxAnisotropy();

    texture.repeat.set(8, 8);
    texture.wrapS = texture.wrapT = THREE.MirroredRepeatWrapping;

    //var geometry = new THREE.BoxBufferGeometry( 200, 200, 200 );
    var material = new THREE.MeshLambertMaterial( { map: texture, transparent: false } );
    material.side = THREE.DoubleSide;
    //material.needsUpdate = true;
    //material.shading = THREE.FlatShading;

    //var geometry = (new QDa()).getGeometry();
    var geometry = this.props.shape.getGeometry();


    orbit = new OrbitControls( currentCamera, renderer.domElement );
    orbit.update();
    orbit.addEventListener( 'change', render );

    control = new TransformControls( currentCamera, renderer.domElement );
    control.addEventListener( 'change', render );
    control.addEventListener( 'dragging-changed', function ( event ) {

      orbit.enabled = ! event.value;

    } );

    var mesh = new THREE.Mesh( geometry, material );

    scene.add( mesh );
    //scene.add( new FaceNormalsHelper( mesh ) );


    control.attach( mesh );
    scene.add( control );

    window.addEventListener( 'resize', onWindowResize, false );

    window.addEventListener( 'keydown', function ( event ) {

      switch ( event.keyCode ) {

        case 81: // Q
          control.setSpace( control.space === "local" ? "world" : "local" );
          break;

        case 16: // Shift
          control.setTranslationSnap( 100 );
          control.setRotationSnap( THREE.MathUtils.degToRad( 15 ) );
          control.setScaleSnap( 0.25 );
          break;

        case 87: // W
          control.setMode( "translate" );
          break;

        case 69: // E
          control.setMode( "rotate" );
          break;

        case 82: // R
          control.setMode( "scale" );
          break;

        case 67: // C
          const position = currentCamera.position.clone();

          currentCamera = currentCamera.isPerspectiveCamera ? cameraOrtho : cameraPersp;
          currentCamera.position.copy( position );

          orbit.object = currentCamera;
          control.camera = currentCamera;

          currentCamera.lookAt( orbit.target.x, orbit.target.y, orbit.target.z );
          onWindowResize();
          break;

        case 86: // V
          const randomFoV = Math.random() + 0.1;
          const randomZoom = Math.random() + 0.1;

          cameraPersp.fov = randomFoV * 160;
          cameraOrtho.bottom = - randomFoV * 500;
          cameraOrtho.top = randomFoV * 500;

          cameraPersp.zoom = randomZoom * 5;
          cameraOrtho.zoom = randomZoom * 5;
          onWindowResize();
          break;

        case 187:
        case 107: // +, =, num+
          control.setSize( control.size + 0.1 );
          break;

        case 189:
        case 109: // -, _, num-
          control.setSize( Math.max( control.size - 0.1, 0.1 ) );
          break;

        case 88: // X
          control.showX = ! control.showX;
          break;

        case 89: // Y
          control.showY = ! control.showY;
          break;

        case 90: // Z
          control.showZ = ! control.showZ;
          break;

        case 32: // Spacebar
          control.enabled = ! control.enabled;
          break;

      }

    } );

    window.addEventListener( 'keyup', function ( event ) {

      switch ( event.keyCode ) {

        case 16: // Shift
          control.setTranslationSnap( null );
          control.setRotationSnap( null );
          control.setScaleSnap( null );
          break;

      }

    } );

    function render() {
          renderer.render( scene, currentCamera );
    }


    function onWindowResize() {

        const aspect = window.innerWidth / window.innerHeight;
        cameraPersp.aspect = aspect;
        cameraPersp.updateProjectionMatrix();

        cameraOrtho.left = cameraOrtho.bottom * aspect;
        cameraOrtho.right = cameraOrtho.top * aspect;
        cameraOrtho.updateProjectionMatrix();

        renderer.setSize( window.innerWidth, window.innerHeight );

        render();
    }

  }


  render () {
    return (
      <div />
    )
  }
}

export default Scene3DTrasform;
